.nav_menu {
    background-color: var(--container-color);
    position: fixed;
    inset-block: 0;
    left: -150%;
    width: 100%;
    padding: 60px 30px;
    transition: all 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);
    z-index: 10;
}

.show_menu {
    left: 0;
}

.nav_link {
    display: flex;
    align-items: center;
    column-gap: 24px;
    padding-block: 14px;
    border-bottom: 1px solid var(--border-color);
}

.nav_icon,
.item_name {
    color: var(--title-color);
    font-size: 24px;
}

.item_name {
    font-weight: var(--weight-500);
}

.active .nav_icon,
.active .item_name {
    color: var(--first-color);
}

.toggle {
    background-color: var(--container-color);
    position: fixed;
    top: 30px;
    right: 30px;
    width: 40px;
    height: 40px;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    z-index: 10;
}

.toggle span {
    display: block;
    width: 20px;
    height: 2px;
    background-color: var(--title-color);
    border-radius: 2px;
    margin-top: 2px;
    margin-bottom: 4px;

}

.animate_toggle span:first-child {
    transform: rotate(45deg) translate(5px, 4px);
    /* transform: rotate(45deg) translate(6px,4px); */
}

.animate_toggle span:nth-child(2) {
    display: none;
}

.animate_toggle span:last-child {
    transform: rotate(-45deg) translate(1px, -1px);
}

@media (min-width: 1024px) {
    .nav_menu {
        all: unset;
    }

    .toggle {
        display: none;
    }

    .nav {
        z-index: 2;
        position: fixed;
        inset-block: 0;
        right: 30px;
        display: flex;
        align-items: center;
    }

    .nav_item {
        margin-block: 20px;
    }

    .nav_link {
        padding: 0;
        border-bottom: none;
        background-color: var(--container-color);
        width: 50px;
        height: 50px;
        border-radius: 50%;
        position: relative;
    }

    .nav_icon {
        font-size: 20px;
        margin-inline: auto;
    }

    .item_name {
        position: absolute;
        top: 0%;
        right: 0;
        background-color: var(--first-color);
        color: var(--text-white);
        height: 100%;
        line-height: 50px;
        padding-inline: 30px 25px;
        border-radius: 30px;
        font-size: var(--small-font-size);
        z-index: -1;
        visibility: hidden;
        transition: all 0.3s ease;
    }

    .nav_link:hover .item_name{
        color: var(--text-white);
        right: 27px;
        opacity: 1;
        visibility: visible;
        border-radius: 30px 0 0 30px;
    }

    .nav_link:hover,
    .active {
        background-color: var(--first-color);
    }

    .nav_link:hover .nav_icon,
    .active .nav_icon {
        color: var(--text-white);
    }

}